<template>
    <div>
        <Topbar/>
        <div class="app-wrapper" :class="{hideSidebar:!sidebar.opened}">
            <div class="sidebar-wrapper">
                <Sidebar class="sidebar-container"/>
            </div>
            <div class="main-container">
                <App-main/>
            </div>
        </div>
    </div>
</template>

<script>
    import {Topbar, Sidebar, AppMain} from 'views/layout';

    export default {
        name: 'layout',
        components: {
            Topbar,
            Sidebar,
            AppMain
        },
        computed: {
            sidebar() {
                return this.$store.state.app.sidebar;
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";

    .app-wrapper {
        @include clearfix;
        position: relative;
        height: 100%;
        width: 100%;
        &.hideSidebar {
            .sidebar-wrapper {
                transform: translate(-140px, 0);
                .sidebar-container {
                    transform: translate(132px, 0);
                }
                &:hover {
                    transform: translate(0, 0);
                    .sidebar-container {
                        transform: translate(0, 0);
                    }
                }
            }
            .main-container {
                margin-left: 70px;
            }
        }
        .sidebar-wrapper {
            width: 200px;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 1001;
            overflow: hidden;
            transition: all .28s ease-out;
        }
        .sidebar-container {
            transition: all .28s ease-out;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            overflow-y: scroll;
        }
        .main-container {
            min-height: 100%;
            transition: all .28s ease-out;
            margin-left: 200px;
        }
    }
</style>


